/* eslint-disable */
import React, { Component } from 'react';
import axios from 'axios';
import { log } from 'util';
import { DateTime } from "luxon";
import { Markup } from 'interweave';
import { Client } from '@stomp/stompjs';
import AddComment from './AddComment';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

import { Editor } from '@tinymce/tinymce-react';
import CKEditor4 from 'ckeditor4-react';
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';



class Comments extends Component {

    //DateTime
    constructor(props) {
        super(props);
        this.fetchComments = this.fetchComments.bind(this);
        this.initSSE = this.initSSE.bind(this);
        this.initSocket = this.initSocket.bind(this);
        this.doUp = this.doUp.bind(this);
        this.doDown = this.doDown.bind(this);
        this.doReport = this.doReport.bind(this);
        this.doEdit = this.doEdit.bind(this);
        this.doDelete = this.doDelete.bind(this);
        this.addComment = this.addComment.bind(this);
        this.editComment = this.editComment.bind(this);
        this.onDoDelete = this.onDoDelete.bind(this);
        this.toggle = this.toggle.bind(this);
        this.toggleEdit = this.toggleEdit.bind(this);

        this.handleSaveEdit = this.handleSaveEdit.bind(this);
        this.onEditorCK4Change = this.onEditorCK4Change.bind(this);
        this.validateInput = this.validateInput.bind(this); 
        this.editCommentByStomp = this.editCommentByStomp.bind(this);
        this.editCommentByRest = this.editCommentByRest.bind(this);

        this.onCancelDelete = this.onCancelDelete.bind(this);
        this.removeComment = this.removeComment.bind(this);
        this.onSaveEdit = this.onSaveEdit.bind(this);

        this.state = {
            buzy: false,
            msg: "",
            comments: [],
            comment: null,
            newComment: null,
            editComment: null,
            commentText:null,
            serverTime: null,
            contextComment: null,
            deleteModal: false,
            editModal:false
        }

        //   var url =`ws://localhost:3000/ws`;
        // this.ws = new WebSocket('ws://localhost:3000/ws')
    }

    toggle(event) {
        this.setState({ deleteModal: !this.state.deleteModal });
    }

    toggleEdit(event) {
        this.setState({ editModal: !this.state.editModal });
    }
    

    doUp(event, item) {
        const { oid } = this.props;
        axios.get(`${this.props.context}/app/content/rest/comment/up/contentoid/${oid}/commentoid/${item.oid}`)
            .then(res => {
                this.setState({ comment: res.data.data });
                console.log("doUp " + res.data.data);
            }).catch(err => {
                alert(JSON.stringify(err));
                console.log(err);
            })
    }


    doDown(event, item) {

        const { oid } = this.props;
        axios.get(`${this.props.context}/app/content/rest/comment/down/contentoid/${oid}/commentoid/${item.oid}`)
            .then(res => {
                this.setState({ comment: res.data.data });
                console.log("doUp " + res.data.data);
            }).catch(err => {
                alert(JSON.stringify(err));
                console.log(err);
            })
    }


    doReport(event, item) {
        const { oid } = this.props;
        axios.get(`${this.props.context}/app/content/rest/comment/report/contentoid/${oid}/commentoid/${item.oid}`)
            .then(res => {
                this.setState({ comment: res.data.data });

                console.log("doReport " + res.data.data);
                alert(" your Report has Been Received");
            }).catch(err => {
                alert(JSON.stringify(err));
                console.log(err);
            })
    }


    doEdit(event, item) { 
 
        this.setState({ editModal: true, contextComment:item });
    }



    onSaveEdit(contextComment) { 
        this.setState(contextComment);
        const{oid} = this.props;

        if(!contextComment){
            alert("comment context can not be null, something went wrong");
            return;
        }


        axios.get(`${this.props.context}/app/content/rest/comment/edit/contentoid/${oid}/commentoid/${contextComment.oid}`)
            .then(res => {
                this.setState({ comment: res.data.data });
                console.log("DoEdit " + res.data.data);

                editComment(contextComment);
            }).catch(err => {
                alert(JSON.stringify(err));
                console.log(err);
            })
    }


 

    doDelete(event, item) {
        const { oid } = this.props;
        this.setState({ deleteModal: true, contextComment:item });
    }



    onCancelDelete(event) { 
        this.setState({ deleteModal: false, contextComment:null });
    }

    onDoDelete(event) { 

        const{contextComment} = this.state;
        const{oid} = this.props;

        if(!contextComment){
            alert("comment context can not be null, something went wrong");
            return;
        }

        axios.get(`${this.props.context}/app/content/rest/comment/delete/contentoid/${oid}/commentoid/${contextComment.oid}`)
            .then(res => {
                this.setState({ comment: res.data.data });
                console.log("doDelete " + res.data.data);
                this.setState({ deleteModal: false, contextComment:null });
                this.removeComment(contextComment);
                console.log(`[ ${contextComment.comment}] has been deleted`);
             
            }).catch(err => {
                alert(JSON.stringify(err));
                console.log(err);
            })
    }

    fetchComments(oid) {
        //${cx}    
        axios.get(`${this.props.context}/app/content/rest/comments/load/contentoid/${oid}`)
            .then(res => {
                var data = res.data.data || [];
                this.setState({ comments: data });
            }).catch(err => {
                alert(JSON.stringify(err));
                console.log(err);
                this.setState({ comments: [] });
            })
    }



    componentDidUpdate(prevProps, prevState) {
        const { comments, comment, newComment, editComment,contextComment } = this.state;
// sss
        if (comments != prevState.comments) {
            // alert(JSON.stringify(comments));
        }

        if (contextComment && contextComment != prevState.contextComment) {
            // alert(JSON.stringify(comments));
            this.setState({commentText:contextComment.comment})
        }


        
        if (newComment != prevState.newComment && newComment) {
            // alert(JSON.stringify(comments));
            this.addComment(newComment);
        }

        if (editComment != prevState.editComment && editComment) {
            // alert(JSON.stringify(comments));
            this.editComment(editComment);
        }
    }


    addComment = (comment) => {
        const list = this.state.comments.concat(comment);
        this.setState({ comments: list });
    };

    editComment = (comment) => {

        // handleCommentEdit: function(id, text) {
        //     this.setState({
        //       data: this.state.data.map(el => (el.id === id ? Object.assign({}, el, { text }) : el))
        //     });
        // }

        this.setState({
            comments: this.state.comments.map(c => (c.oid === comment.oid ? Object.assign({}, comment) : c))
        });

        // this.setState({comments:list});
    };

    removeComment(e) {
        this.setState({comments: this.state.comments.filter(function(c) { 
            return c.oid !== e.oid 
        })});
    }



    initSSE() {
        // this.eventSource = new EventSource('/ui/notification');
        this.eventSource = new EventSource('/ui/sse/comment');



        var zis = this;
        this.eventSource.onmessage = e => {
            const msg = e.data;
            this.setState({ newComment: JSON.parse(msg) });
        };
        this.eventSource.onopen = e => console.log('open');
        this.eventSource.onerror = e => {
            if (e.readyState == EventSource.CLOSED) {
                console.log('close');
            }
            else {
                console.log(e);
            }
        };
        this.eventSource.addEventListener('second', function (e) {
            console.log('second', e.data);
        }, false);
    }


    initSocket() {

        // The compat mode syntax is totally different, converting to v5 syntax
        // Client is imported from '@stomp/stompjs'
        this.client = new Client();

        this.url =`ws://${this.props.host}${this.props.context}/stomp`;
        this.client.configure({
            brokerURL: this.url,
            onConnect: () => {
                console.log('onConnect');

                this.client.subscribe('/topic/comments', message => {
                    // alert('/topic/comments'+JSON.stringify(message));
                    console.log(message)
                    var a = message.headers;
                    if (a && a.operation) {
                        if (a.operation == "update") {
                            this.setState({ editComment: JSON.parse(message.body) });
                        } else {
                            this.setState({ newComment: JSON.parse(message.body) });
                        }
                    } else {
                        this.setState({ newComment: JSON.parse(message.body) });
                    }
                });
            },
            // Helps during debugging, remove in production
            debug: (str) => {
                console.log(new Date(), str);
            }
        });


        this.client.activate();


    }




    componentDidMount() {
        // initSSE();
        this.fetchComments(this.props.oid);

        this.initSocket();
        this.initSSE();
        if (true) return;
        if (this.props.mode && this.props.mode == "rest") {
            this.initSSE();
        } else {
            this.initSocket();
        }
    }



    handleChange = event => {
        event.preventDefault();

        this.setState({
        [event.target.name]: event.target.value,
        });

        // this.validateData();
    }

    handleEditorChange = (e) => {
        console.log('Content was updated:', );
        this.setState({
            commentText: e.target.getContent()
        });
    }

    onEditorCK4Change( evt ) {
        this.setState( {
            commentText: evt.editor.getData()
        } );
    }



    handleSaveEdit = event => { 
// aaa
        if(this.validateInput()) { 
        if(this.props.mode && this.props.mode=="rest"){
            this.editCommentByRest();
        } else  {
            this.editCommentByStomp();
        }


        }
    }


    editCommentByRest() {

        let a = Object.assign({},this.state.contextComment);
            a["content_id"]=this.props.oid;
            a["comment"]=this.state.commentText;
            a["mode"]=this.props.mode || "rest"; 
        axios({
            method: 'POST',
            url: `${this.props.context}/app/content/rest/comment/edit`,
            data: a
        })
        .then((respo) => {
            console.log('data', respo.data);
        //  alert(JSON.stringify(respo.data));
            // this.toDone();
            this.setState({ buzy : false });
            this.setState({commentText:'',editModal:false});
        }).catch((e)=> {
            //alert(JSON.stringify(e));
                this.setState({ buzy : false });
        });
        }

    editCommentByStomp() {
        var client  = this.props.client;
        let a = Object.assign({},this.state.contextComment);
            a["content_id"]=this.props.oid;
            a["comment"]=this.state.commentText;
            a["mode"]=this.props.mode || "stomp"; 
        // this.client.publish({destination: '/add/comment/message', body: JSON.stringify(a)    });
        this.client.publish({destination: '/message/comment/edit/camel', body: JSON.stringify(a)    });

    
        this.setState({commentText:'',editModal:false});
        // alert(`submitted ${this.props.oid} comment: ${this.state.comment}`);
        // if(!this.validateData()){
        //   return;
        // }
      
    }

    validateInput() {

        if(!this.state.commentText || this.state.commentText.trim()==""){
            alert("please enter comment");
            return false;
        }
        return true;
    }
 


    render() {

        const { deleteModal , editModal} = this.state;
        const zis = this;
        return (
            <div>

              
                <Modal isOpen={deleteModal} toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Modal title</ModalHeader>
                    <ModalBody>
                        Are you Sure you want to delete, following comment: 
                        <pre>{this.state.contextComment && 
                        <Markup content={this.state.contextComment.comment} />}</pre>
                        </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.onDoDelete}>Yes</Button>{' '}
                        <Button color="secondary" onClick={this.onCancelDelete}>Cancel</Button>
                    </ModalFooter>
                </Modal>


                <Modal isOpen={editModal} toggle={this.toggleEdit} className={this.props.className}>
                    <ModalHeader toggle={this.toggleEdit}>Editing Comment... {this.state.contextComment && this.state.contextComment.oid}</ModalHeader>
                    <ModalBody>  

                    <div className="media mb-2">
                                <img src="ui/images/users/avatar-1.jpg" height="32" className="align-self-start rounded mr-2" alt="Arya Stark" />
                                <div className="media-body">

                                    <div className="border rounded"> 
                                    
                                    {this.props.editor && this.props.editor=="tiny" && 
                                        <Editor 
                                                initialValue={zis.state.commentText}
                                                init={{
                                                menubar: false,
                                                plugins: [
                                                    'advlist autolink lists link image charmap anchor',
                                                    'searchreplace visualblocks   fullscreen emoticons' ,
                                                    'insertdatetime media table paste code   wordcount'
                                                ],
                                                toolbar:
                                                    'emoticons | undo redo | formatselect | bold italic backcolor | \
                                                    alignleft aligncenter alignright alignjustify | \
                                                    bullist numlist outdent indent | removeformat |  '
                                                }}
                                                onChange={this.handleEditorChange.bind(this)}   
                                            />
                                    }



                                    {this.props.editor && this.props.editor=="ck4" && 
                                    <div style={{overflow: 'auto'}} >
                                        <CKEditor4 onBeforeLoad={ ( CKEDITOR ) => ( CKEDITOR.disableAutoInline = true ) }
                                        data={this.state.commentText}
                                        onChange={this.onEditorCK4Change} />
                                        
                                        </div>
                                    }


                                    {this.props.editor && this.props.editor=="ck5" && 
                                                <CKEditor
                                                editor={ ClassicEditor }
                                                data={this.state.commentText}
                                                onInit={ editor => {
                                                    // You can store the "editor" and use when it is needed.
                                                    console.log( 'Editor is ready to use!', editor );
                                                } }
                                                onChange={ ( event, editor ) => {
                                                    const data = editor.getData();
                                                    console.log( { event, editor, data } );
                                                } }
                                                onBlur={ ( event, editor ) => {
                                                    this.setState({commentText:editor.getData()});
                                                } }
                                                onFocus={ ( event, editor ) => {
                                                    console.log( 'Focus.', editor );
                                                } }
                                            />
                                    } 
                                    </div>
                                </div> 
                            </div> 
                        </ModalBody>
                    <ModalFooter>
                    <div className="p-2 bg-light d-flex justify-content-between align-items-center">
                        <div>
                            <a href="#" className="btn btn-sm px-2 font-16 btn-light"><i className="uil uil-scenery"></i></a>
                            <a href="#" className="btn btn-sm px-2 font-16 btn-light"><i className="uil uil-location"></i></a>
                            <a href="#" className="btn btn-sm px-2 font-16 btn-light"><i className="uil uil-paperclip"></i></a>
                        </div>
                        <button type="submit" onClick={this.handleSaveEdit}  className="btn btn-sm btn-success"><i className="uil uil-message mr-1"></i>Save Comment</button>
                    </div>
                    </ModalFooter>
                </Modal>

                <div> <span className="badge badge-info">{this.props.mode}</span><span className="badge badge-info">{this.url}</span>
              
                </div>
                <h1>{this.state.msg}</h1>
                {this.state.comments.map((item, key) => {

                    return (
                        <div key={item.oid} >

                            <div className="media">
                                <img className="mr-2 rounded" src={"/ui/images/users/avatar_m.png"} alt="Generic placeholder image" height="32" />
                                <div className="media-body">
                                    <h5 className="m-0">  {item.user.name}</h5>
                                    <p className="text-muted mb-0"><small> {DateTime.fromISO(item.createdOn).toRelative()} </small></p>

                                    <div className="my-1"><Markup content={item.comment} /> </div>

                                    <div>
                                        <div>total votes: {item.totalvotes}</div>
                                        <div>total vote values: {item.totalvotessum}</div>
                                        <button onClick={(e) => { this.doUp(e, item) }} className="btn btn-sm btn-success"><i className="uil uil-message mr-1"></i>up</button>
                                        <button onClick={(e) => { this.doDown(e, item) }} className="btn btn-sm btn-success"  ><i className="uil uil-message mr-1"></i>down</button>
                                        <button onClick={(e) => { this.doReport(e, item) }} className="btn btn-sm btn-success"><i className="uil uil-message mr-1"></i>report</button>
                                        <button onClick={(e) => { this.doEdit(e, item) }} className="btn btn-sm btn-success"><i className="uil uil-message mr-1"></i>edit</button>
                                        <button onClick={(e) => { this.doDelete(e, item) }} className="btn btn-sm btn-success"><i className="uil uil-message mr-1"></i>delete</button>
                                    </div>
                                </div>
                            </div>
                            <hr />
                        </div>
                    );
                })}
                <AddComment {...this.props} />

            </div>

        );
    }
}

export default Comments;
