/* eslint-disable */
import React, { Component } from 'react';
import { Client } from '@stomp/stompjs';
import axios from 'axios';
import { Editor } from '@tinymce/tinymce-react';
import CKEditor4 from 'ckeditor4-react';
import CKEditor from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

class AddComment extends Component {
    constructor(props) {
        super(props);
        this.state = {  
            commentText:''
         };


        this.handleSubmit = this.handleSubmit.bind(this);
        this.onEditorCK4Change = this.onEditorCK4Change.bind(this);
        this.handleChange =this.handleChange.bind(this);
        this.initSocket = this.initSocket.bind(this); 
        this.validateInput = this.validateInput.bind(this); 
        this.addCommentByStomp = this.addCommentByStomp.bind(this);
        this.addCommentByRest = this.addCommentByRest.bind(this);


        // <AddComment {...this.props} contextComment={this.state.contextComment} mode="edit" onSave={this.onSaveEdit} />

    }
 


    initSocket() {
          
        // The compat mode syntax is totally different, converting to v5 syntax
        // Client is imported from '@stomp/stompjs'
       this.client = new Client();


        var url =`ws://${this.props.host}${this.props.context}/stomp`;
       this.client.configure({
           brokerURL: url,
           onConnect: () => {
             console.log('onConnect');
     
             this.client.subscribe('/topic/comments', message => {
               console.log(message);
               // alert(JSON.stringify(message.body))
             });
     
        
           },
           // Helps during debugging, remove in production
           debug: (str) => {
             console.log(new Date(), str);
           }
         });
         this.client.activate();
    }

   
    componentDidUpdate(prevProps, prevState, snapshot){
        const { commentText} = this.state;
        
    }


    componentDidMount() {

        this.initSocket();


    }
    


    handleChange = event => {
        event.preventDefault();

        this.setState({
        [event.target.name]: event.target.value,
        });

        // this.validateData();
    }

    handleEditorChange = (e) => {
        console.log('Content was updated:', );
        this.setState({
            commentText: e.target.getContent()
        });
    }

    onEditorCK4Change( evt ) {
        this.setState( {
            commentText: evt.editor.getData()
        } );
    }

 


    //   {"content_id":"this.props.oid","user":{"name":"mutela"},
    //     "comment":"this.state.comment"}
    handleSubmit = event => {
        event.preventDefault();

        if(this.validateInput()) {
            if(this.props.mode && this.props.mode=="rest"){
                this.addCommentByRest();
            }else  {
                this.addCommentByStomp();
            }
        }
    }


    addCommentByRest() {

        let a = {};
            a["content_id"]=this.props.oid;
            a["comment"]=this.state.commentText;
            a["mode"]=this.props.mode || "rest"; 
        axios({
            method: 'POST',
            url: `${this.props.context}/app/content/rest/comment/add`,
            data: a
        })
        .then((respo) => {
            console.log('data', respo.data);
        //  alert(JSON.stringify(respo.data));
            // this.toDone();
            this.setState({ buzy : false });
            this.setState({commentText:''});
        }).catch((e)=> {
            //alert(JSON.stringify(e));
                this.setState({ buzy : false });
        });
        }

    addCommentByStomp() {
        var client  = this.props.client;
        let a = {};
            a["content_id"]=this.props.oid;
            a["comment"]=this.state.commentText;
            a["mode"]=this.props.mode || "stomp"; 
        // this.client.publish({destination: '/add/comment/message', body: JSON.stringify(a)    });
        this.client.publish({destination: '/message/comment/add/camel', body: JSON.stringify(a)    });

    
        this.setState({commentText:''});
        // alert(`submitted ${this.props.oid} comment: ${this.state.comment}`);
        // if(!this.validateData()){
        //   return;
        // }
      
    }

    validateInput() {

        if(!this.state.commentText || this.state.commentText.trim()==""){
            alert("please enter comment");
            return false;
        }
        return true;
    }


    render() {
        const zis = this;
        return (
            <div className="media mb-2">
            <img src="ui/images/users/avatar-1.jpg" height="32" className="align-self-start rounded mr-2" alt="Arya Stark" />
            <div className="media-body">

                <div className="border rounded">
                    <form action="#" id="commentForm" className="comment-area-box" noValidate onSubmit={this.handleSubmit} >
                
                {this.props.editor && this.props.editor=="tiny" && 
                    <Editor 
                            initialValue={this.state.commentText}
                            init={{
                            menubar: false,
                            plugins: [
                                'advlist autolink lists link image charmap anchor',
                                'searchreplace visualblocks   fullscreen emoticons' ,
                                'insertdatetime media table paste code   wordcount'
                            ],
                            toolbar:
                                'emoticons | undo redo | formatselect | bold italic backcolor | \
                                alignleft aligncenter alignright alignjustify | \
                                bullist numlist outdent indent | removeformat |  '
                            }}
                            onChange={this.handleEditorChange.bind(this)}   
                        />
                }



                {this.props.editor && this.props.editor=="ck4" && 
                <div style={{overflow: 'auto'}} >
                    <CKEditor4 onBeforeLoad={ ( CKEDITOR ) => ( CKEDITOR.disableAutoInline = true ) }
                    data={this.state.commentText}
                    onChange={this.onEditorCK4Change} />
                     
                    </div>
                }


                {this.props.editor && this.props.editor=="ck5" && 
                            <CKEditor
                            editor={ ClassicEditor }
                            data={this.state.commentText}
                            onInit={ editor => {
                                // You can store the "editor" and use when it is needed.
                                console.log( 'Editor is ready to use!', editor );
                            } }
                            onChange={ ( event, editor ) => {
                                const data = editor.getData();
                                console.log( { event, editor, data } );
                            } }
                            onBlur={ ( event, editor ) => {
                                this.setState({commentText:editor.getData()});
                            } }
                            onFocus={ ( event, editor ) => {
                                console.log( 'Focus.', editor );
                            } }
                        />
                } 
                       <div className="p-2 bg-light d-flex justify-content-between align-items-center">
                            <div>
                                <a href="#" className="btn btn-sm px-2 font-16 btn-light"><i className="uil uil-scenery"></i></a>
                                <a href="#" className="btn btn-sm px-2 font-16 btn-light"><i className="uil uil-location"></i></a>
                                <a href="#" className="btn btn-sm px-2 font-16 btn-light"><i className="uil uil-paperclip"></i></a>
                            </div>
                            <button type="submit" className="btn btn-sm btn-success"><i className="uil uil-message mr-1"></i>Post Comment</button>
                        </div>
                    </form>
                </div>

            </div> 
        </div> 
        );
    }
}

export default AddComment;